.site-nav-toggle {
    border: 0 none;
    background-color: #000;
    position: relative;
    //display: block;
    display: none;
    
    $target-size: 44px;
    $bar-height: 2px;
    width: $target-size;
    height: $target-size;
    span {
        &, &:before, &:after {
            display: block;
            position: absolute;
            width: $target-size * 0.5;
            height: $bar-height;
            background-color: #FFF;
            transition: transform 0.66s;
            -webkit-transform: perspective(1px) scale(1.1) translate3d(0,0,0);
        }
        & { 
            left: ($target-size / 4);
            top: ($target-size / 2) - ($bar-height / 2);
        }
        &:before, &:after { content: ' '; }
        &:before { top: $target-size / 4.4; }
        &:after { bottom: $target-size / 4.4; }
    }
    
    // Transform into isosceles triangle
    &:hover span {
        &, &:before, &:after { border-radius: 2pt; }
        &:before { transform: rotate(60deg) translate(-4px,4px); }
        & { transform: translate(0,-7px); }
        &:after { transform: rotate(-60deg) translate(-14px,14px); }
    }
}

.site-nav {
    a { text-decoration: none; }
    ul { 
        list-style: none; 
        margin: 0; 
        padding: 0; 
    }
    
    // Top-level navigation
    > ul {
        > li {
            float: left;
            width: 50%; 
            &:nth-child(2n) {
                text-align: right;
            }
            @include susy-breakpoint(800px) {
                &:nth-child(-n+2) {
                    float: left;
                    text-align: left;
                    width: 22.2222%; 
                }
                &:nth-last-child(-n+2) {
                    float: right;
                    text-align: right;
                    width: 22.2222%; 
                }
            }
            //display: inline-block;
            position: relative;
            vertical-align: top;
            a { 
                display: block;
                small {
                    font-size: 8pt;
                    display: block;
                    color: palette-tint(primary, 75%);
                    margin-top: 2pt;
                }
                &, & small { transition: all 0.3s ease; }
            }
            > a { 
                font: 300 10pt/1.2 'raleway-web', sans-serif;
                @include susy-breakpoint(955px) {
                    font-size: 12pt;
                }
                height: 66px;
                padding: 0 8pt;
                color: #FFF;
                text-transform: uppercase;
                span {
                    display: block;
                    padding-top: 18px;
                }
                &:hover {
                    background-color: palette-tint(primary, 45%);
                    small { color: palette-tint(primary, 80%); }
                }
                &:focus, &:active {
                    background-color: palette-tint(primary, 80%);
                    color: palette(primary);
                    small { color: palette(primary); }
                }
                &.active {
                    background-color: #FFF;
                    color: palette(primary);
                    small { color: palette-tint(primary, 25%); }
                }
            }
