body {
    font: 300 13pt/1.6 Lato, sans-serif;
    color: palette(body);
}

a {
    color: inherit;
    text-decoration: none;
    p &, .main-content & { @include link; }
}

h1, h2, h3, h4, h5, h6 {
    color: palette(body);
    font-family: 'raleway-web', sans-serif;
    font-weight: 600;
    line-height: 1.2;
    a {
        text-decoration: none;
        color: inherit;
    }
    .main-content & a {
        box-shadow: none;
        &::before {
            transform: translateX(1em);
            transition: transform .66s cubic-bezier(0.215, 0.61, 0.355, 1), opacity .33s cubic-bezier(0.455, 0.03, 0.515, 0.955);
            @include icon(link);
            opacity: 0;
            margin-left: -1.2em;
            color: palette-tint(tertiary);
        }
        &:hover, &:focus, &:active {
            background: none;
            &::before {
                transform: translateX(0);
                opacity: 1;
            }
        }
    }
    small {
        font-size: 0.61538em;
        font-weight: 400;
        line-height: 1em;
        display: block;
        margin-top: 0.25em; 
    }
}

.h-major {
    overflow: hidden;
    text-align: center;
    &-around > span {
        display: inline-block;
        position: relative; 
        &::before, &::after {
            background-color: palette-tint(primary);
            content: "";
            display: block;
            height: 2px;
            margin-top: -1px;
            width: susy-get(container);
            position: absolute;
            top: 50%; 
        }
        &::before {
            left: 100%;
            margin-left: 1em; 
        }
        &::after {
            right: 100%;
            margin-right: 1em; 
        }
    }
}

ul, ol { padding-left: 0; }

.intro {
    position: relative;
    .fade-gradient {
        z-index: 0;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(to bottom, rgba(119,155,155,0.5) 0%,rgba(213,211,255,0.5) 100%);
    }
    img { margin-bottom: 1.5em; }
    .wrap { position: relative; z-index: 2; }
}

.main-content ul {
    list-style: none;
    li {
        position: relative;
        + li { margin-top: .15em; }
    }

    li::before {
        content: "·";
        font-size: 1.5em;
        vertical-align:middle;
        line-height:20px;
        position: absolute;
        top: .15em;
        left: -.75em;
    }
}

.wrap {
    @include container;
}

main > *, .mainfooter {
    $margin-height: 18pt;
    padding-top: $margin-height;
    padding-bottom: $margin-height;
    @include susy-breakpoint(max-width susy-get(container)) {
         padding-left: $margin-height;
         padding-right: $margin-height;
    }
}

.lead { font-size: 115%; }

.catchword {
    font-size: .7333em;
    font-weight: 200;
    vertical-align: 0.1335em;
    color: palette(primary);
}

.page-hr {
    margin: 0 auto .75em;
    border: none;
    border-bottom: 2px solid palette(primary);
    padding: 0;
    width: 4.44em;
}

.page-aside {
    line-height: 1.3;
    float: right;
    margin: -.5em 0 1em 1em;
    padding: 1em 1em 1em 2em;
    background-color: palette-tint(secondary, 80%);
    h1 {
        font-size: inherit;
        font-family: inherit;
        text-transform: uppercase;
        letter-spacing: 2pt;
        color: palette-shade(secondary, 80%);
    }
    > * { margin: 0; }
    > * + * { margin-top: 1em; }
    ul > li a { @include link($tint: 25%); }
}

.stats {
    list-style: none;
    margin: 0;
    &-about {
        > * {
            @include susy-breakpoint(400px) {
                @include gallery(6);
            }
            @include susy-breakpoint(800px) {
                @include gallery(3);
            }
        }
    }
    color: #FFF;
    font: 200 14pt/1.2 'raleway-web', sans-serif;
    text-shadow: palette(body) 0 1px 4px;
    text-transform: uppercase;
    letter-spacing: 8pt;
    span {
        letter-spacing: normal;
        font-size: 72pt;
        font-weight: 900;
        display: block;
    }
}

.hero-map {
    width:100%;
    height:300px;
}

.clubs-map {
    @include susy-breakpoint(800px) {
        height: 500px;
    }
}